/*libs*/
/* main */
* {
  -webkit-backface-visibility: hidden;
}

body {
  overflow-y: scroll;
  overflow-x: hidden;
}

body.active {
  overflow: hidden;
}

.wrapper {
  display: flex;
  flex-direction: column;
  height: 100%;
}
.wrapper .main {
  flex: 1 1 auto;
}

.container {
  width: 1195px;
  padding: 0 15px;
  margin: 0 auto;
  max-width: 100%;
}
@media (max-width: 768px) {
  .container {
    padding: 0 4vw;
  }
}

.swiper-pagination-current::after {
  content: "f";
  text-transform: lowercase;
  opacity: 0;
}

.swiper-pagination-total::before {
  content: "f";
  text-transform: lowercase;
  opacity: 0;
}

.general-title {
  display: inline-flex;
  align-items: center;
  font-family: "Muller";
  font-style: normal;
  font-weight: 700;
  font-size: 48px;
  line-height: 120%;
  color: #101010;
  position: relative;
}
@media (max-width: 1100px) {
  .general-title {
    font-size: 36px;
  }
}
@media (max-width: 768px) {
  .general-title {
    font-size: 6.4vw;
  }
}
.general-title img {
  margin-right: 10px;
  position: relative;
  bottom: 5px;
}
@media (max-width: 768px) {
  .general-title img {
    margin-right: 1.333vw;
    width: 3.2vw;
    height: 5.8vw;
    bottom: 0.4vw;
  }
}
.general-title-line {
  position: absolute;
  top: 50%;
  left: calc(100% + 40px);
  height: 1px;
  background-color: #D24318;
}
@media (max-width: 768px) {
  .general-title-line {
    left: calc(100% + 2.667vw);
    height: 0.267vw;
  }
}

.show {
  opacity: 0;
  transition: opacity 1s ease 0s;
}

.show.active {
  opacity: 1;
}

.ibg {
  position: relative;
}
.ibg img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

/* --------hero-------------hero------------ */
.hero {
  padding: 180px 0 95px;
  background: url("../images/hero/img.jpg") center/cover no-repeat;
}
@media (max-width: 768px) {
  .hero {
    background: url("../images/hero/img-mobile.jpg") center/100% 100% no-repeat;
    padding: 24vw 0 13.333vw;
  }
}
.hero__body {
  font-style: normal;
  color: #FFFFFF;
}
.hero__title {
  border: 5px solid #FFFFFF;
  display: inline-block;
  padding: 32px 32px 22px;
  font-family: "Muller";
  font-weight: 800;
  font-size: 60px;
  line-height: 100%;
  color: #FFFFFF;
  text-transform: uppercase;
}
@media (max-width: 768px) {
  .hero__title {
    display: flex;
    justify-content: center;
    width: 100%;
    border: 1.333vw solid #FFFFFF;
    font-weight: 800;
    padding: 5.333vw 0 4.533vw;
    font-size: 8.5vw;
  }
}
.hero__subtitle {
  margin-top: 25px;
  font-family: "Muller";
  font-style: normal;
  font-weight: 500;
  font-size: 40px;
  line-height: 100%;
  color: #FFFFFF;
}
.hero__subtitle br {
  display: none;
}
@media (max-width: 768px) {
  .hero__subtitle {
    margin-top: 5.333vw;
    font-size: 5.333vw;
  }
  .hero__subtitle br {
    display: block;
  }
}
.hero__list {
  margin-top: 68px;
}
@media (max-width: 768px) {
  .hero__list {
    margin-top: 8vw;
  }
}
.hero__item {
  font-family: "TT Norms";
  font-weight: 300;
  font-size: 20px;
  line-height: 120%;
  display: flex;
  align-items: center;
}
@media (max-width: 768px) {
  .hero__item {
    font-size: 4.267vw;
    line-height: 118.75%;
  }
}
.hero__item span {
  margin-left: 8px;
  display: inline-block;
}
@media (max-width: 768px) {
  .hero__item span {
    margin-left: 2.267vw;
  }
}
.hero__item:not(:first-child) {
  margin-top: 9px;
}
@media (max-width: 768px) {
  .hero__item:not(:first-child) {
    margin-top: 2.667vw;
  }
}
.hero__btns {
  margin-top: 60px;
}
@media (max-width: 768px) {
  .hero__btns {
    margin-top: 58.133vw;
    display: flex;
    flex-direction: column;
  }
}
.hero__btns--check {
  border: 2px solid #FFFFFF;
  width: 202px;
  transition: all 0.3s ease 0s;
}
.hero__btns--check:hover {
  border: 2px solid #D24318;
  color: #D24318;
}
@media (max-width: 768px) {
  .hero__btns--check {
    order: 2;
    margin-top: 5.333vw;
  }
}
.hero__btns--buy {
  display: inline-block;
  margin-left: 30px;
  background-color: #D24318;
  width: 170px;
  transition: all 0.3s ease 0s;
}
@media (min-width: 769px) {
  .hero__btns--buy:hover {
    background-color: #FF7146;
  }
}
@media (max-width: 768px) {
  .hero__btns--buy {
    margin-left: 0;
    order: 1;
  }
}
.hero__btn {
  padding: 18px 15px;
  font-family: "TT Norms";
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 116.66%;
  text-align: center;
  text-transform: uppercase;
  color: #FFFFFF;
}
@media (max-width: 768px) {
  .hero__btn {
    width: 100%;
    padding: 4.8vw 0;
    font-size: 4.8vw;
  }
}

/* ---------hugge-------------hugge--------- */
.hugge {
  background-color: #F6F6F6;
  padding: 88px 0 104px;
  position: relative;
  /*&__popap-container.active {
  	transition: opacity 0.3s ease 0s;
  	opacity: 1;
  	pointer-events: auto;
  	overflow: auto;
  }*/
}
@media (max-width: 768px) {
  .hugge {
    padding: 10.667vw 0;
    overflow: visible;
  }
}
.hugge__overflow {
  overflow: hidden;
}
.hugge__popap-back {
  cursor: pointer;
  position: fixed;
  z-index: 101;
  top: 0%;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  background-color: rgba(0, 0, 0, 0.4);
  transition: opacity 0.3s ease 0s, top 0.3s ease 0.3s;
  pointer-events: none;
}
.hugge__popap-back.active {
  transition: opacity 0.3s ease 0s;
  top: 0;
  opacity: 1;
  pointer-events: auto;
}
@media (max-width: 768px) {
  .hugge__popap-back.active {
    z-index: 96;
  }
}
.hugge__popap-wrapper {
  top: 0;
  left: 0;
  position: fixed;
  overflow: auto;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: opacity 0.3s ease 0s;
  pointer-events: none;
  opacity: 0;
  z-index: 102;
}
.hugge__popap-wrapper.active {
  opacity: 1;
  pointer-events: auto;
}
@media (max-width: 768px) {
  .hugge__popap-wrapper {
    z-index: 97;
    padding: 7.6vw 0 0 0;
    top: 0 !important;
    left: 0;
    transform: translate(0%, 0%) !important;
    background-color: #ffffff;
  }
}
.hugge__popap-container {
  max-height: 100%;
  opacity: 1;
  padding: 0;
  z-index: 102;
  width: 965px;
  max-width: 100%;
  overflow: auto;
}
@media (max-width: 768px) {
  .hugge__popap-container {
    position: relative;
    z-index: 97;
    overflow-x: hidden;
  }
}
.hugge__popap-container::-webkit-scrollbar {
  width: 0px;
}
.hugge__popap-words {
  position: relative;
  background-color: #ffffff;
  height: 100%;
  display: flex;
}
@media (max-width: 768px) {
  .hugge__popap-words {
    flex-direction: column;
  }
}
.hugge__popap-words span {
  cursor: pointer;
  position: absolute;
  top: 12px;
  right: 12px;
  width: 32px;
  height: 32px;
  display: inline-block;
  background-color: #ffffff;
  border-radius: 50%;
  z-index: 99;
}
@media (max-width: 768px) {
  .hugge__popap-words span {
    position: fixed;
    width: 8.533vw;
    height: 8.533vw;
    top: 12vw;
    right: 4vw;
  }
}
.hugge__popap-words span::before, .hugge__popap-words span::after {
  content: "";
  position: absolute;
  top: 50%;
  width: 16px;
  height: 2px;
  background-color: #636363;
}
@media (max-width: 768px) {
  .hugge__popap-words span::before, .hugge__popap-words span::after {
    width: 4.267vw;
  }
}
.hugge__popap-words span::before {
  left: 25%;
  transform: rotate(45deg);
}
.hugge__popap-words span::after {
  right: 25%;
  transform: rotate(-45deg);
}
.hugge__popap-img {
  flex: 0 0 340px;
  background: url("../images/hugge/popup-img.webp") 0 0/100% auto no-repeat;
}
@media (max-width: 992px) {
  .hugge__popap-img {
    flex: 0 0 300px;
  }
}
@media (max-width: 768px) {
  .hugge__popap-img {
    flex: 0 0 130vw;
  }
}
.hugge__popap-body {
  padding: 20px 20px 30px 60px;
  font-style: normal;
  font-family: "Muller";
  display: flex;
  flex-direction: column;
  overflow-x: auto;
}
@media (max-width: 992px) {
  .hugge__popap-body {
    padding: 2.5vw;
  }
}
@media (max-width: 840px) {
  .hugge__popap-body {
    padding: 2vw;
  }
}
@media (max-width: 768px) {
  .hugge__popap-body {
    overflow-x: visible;
    padding: 4vw 4vw 5.333vw;
  }
}
.hugge__popap-body::-webkit-scrollbar {
  width: 0px;
}
.hugge__popap-title {
  font-weight: 700;
  font-size: 32px;
  line-height: 120%;
  color: #101010;
}
@media (max-width: 992px) {
  .hugge__popap-title {
    font-size: 3vw;
  }
}
@media (max-width: 768px) {
  .hugge__popap-title {
    font-size: 5.333vw;
  }
}
.hugge__popap-text {
  margin-top: 20px;
  flex: 1 1 auto;
  font-family: "TT Norms";
  font-weight: 400;
  font-size: 16px;
  line-height: 125%;
  color: #101010;
}
@media (max-width: 992px) {
  .hugge__popap-text {
    margin-top: 3vw;
    font-size: 14px;
  }
}
@media (max-width: 840px) {
  .hugge__popap-text {
    margin-top: 2vw;
  }
}
@media (max-width: 768px) {
  .hugge__popap-text-desktop {
    display: none;
  }
}
.hugge__popap-text-mobile {
  display: none;
}
@media (max-width: 768px) {
  .hugge__popap-text-mobile {
    display: block;
  }
}
@media (max-width: 768px) {
  .hugge__popap-text {
    font-size: 4.267vw;
    margin-top: 1.333vw;
    line-height: 118%;
  }
}
.hugge__popap-text p:not(:first-child) {
  margin-top: 1.2em;
}
@media (max-width: 992px) {
  .hugge__popap-text p:not(:first-child) {
    margin-top: 0.8em;
  }
}
@media (max-width: 768px) {
  .hugge__popap-text p:not(:first-child) {
    margin-top: 6.667vw;
  }
}
.hugge__popap-name {
  margin-top: 30px;
  font-weight: 700;
  font-size: 18px;
  line-height: 100%;
  color: #101010;
}
@media (max-width: 992px) {
  .hugge__popap-name {
    margin-top: 3.3vw;
    font-size: 2vw;
  }
}
@media (max-width: 768px) {
  .hugge__popap-name {
    margin-top: 8vw;
    font-size: 4vw;
  }
}
.hugge__container {
  display: flex;
  position: relative;
}
@media (max-width: 768px) {
  .hugge__container {
    flex-direction: column;
  }
}
.hugge__swiper-container {
  background-color: #E5E5E5;
  position: relative;
  flex: 0 1 580px;
  width: 0;
}
@media (max-width: 768px) {
  .hugge__swiper-container {
    flex-basis: auto;
    width: 100%;
    order: 2;
    margin-top: 12vw;
  }
}
.hugge__swiper {
  background-color: #E5E5E5;
  height: 100%;
