@font-face {
	font-family: 'Muller';
	src: url('../fonts/Muller_extraBold/Muller_extraBold.eot');
	src: local('Muller_extraBold'), local('../fonts/Muller_extraBold/Muller_extraBold'),
		url('../fonts/Muller_extraBold/Muller_extraBold.eot?#iefix') format('embedded-opentype'),
		url('../fonts/Muller_extraBold/Muller_extraBold.woff') format('woff'),
		url('../fonts/Muller_extraBold/Muller_extraBold.ttf') format('truetype');
	font-weight: 800;
	font-style: normal;
	font-display: swap;
}

